<template>
  <div class="col l4 m6 s12">
    <div class="card scale-transition hoverable scale-out">
      <div class="card-image">
        <img :src="'static/img/membres/' + photo" class="clickable">
        <span class="card-title">{{ blaz }}</span>
      </div>
      <div class="card-content center-align">
        <p class="member-description">{{ role }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Member',
  props: ['blaz', 'role', 'photo', 'description']
}
</script>

<style scoped>
</style>



