<template>
  <div id="home-vue">
    <img id="oliphant" :src="require('@/assets/img/logo.png')"/>
  </div>
</template>

<script>
export default {
  name: 'Home'
}
</script>

<style scoped>
#home-vue {

}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  15% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

#oliphant {
  max-height: 100%;
  max-width: 100%;
	position:absolute;
	top: 50%;
	transform: translateY(-50%) translateX(-50%);
	left:50%;
  animation-name: fadeIn;
  animation-duration: 5s;
}
</style>



