<template>
  <div class="row">
    <carousel :perPage="1" :navigationEnabled="true" :minSwipeDistance="50" :loop="true">
      <slide v-for="(event, index) of events" :key="index">
        <Event v-bind="event"/>
      </slide>
    </carousel>
  </div>
</template>

<script>
import Event from '@/components/client/Event'
import { Carousel, Slide } from 'vue-carousel'

export default {
  name: 'Events',
  components: {
    Event,
    Carousel,
    Slide
  },
  data () {
    return {
      events: [
        {nom: 'P\'tit dej lundi de 7h à 18h30 @Hall', photo: require('@/assets/img/events/ptitdej.jpg'), description: `P'tit dej : n.m. voir 'Paradis'<br>
Que dire de plus ? Lundi, toute la journée, toute l'imag pourra venir manger gratuitement.<br>
Il y aura de tout, du sucré, du salé, du vegan, de la viande, bref : pour tous les goûts !<br>
Venez vous régaler à nos côtés !`},
        {nom: 'KFET mercredi 18h30 @Minp', photo: require('@/assets/img/events/minp.jpg'), description: `PAF : 7€<br>Bienvenue à l’Auberge du Poney Fringant !<br>
Les seigneurs de la Kfet sont heureux de vous convier au repas qu’ils vous ont concocté, un festin digne des plus grands ! Que vous soyez un nain carnassier ou un elfe aux goûts variés, vous trouverez votre bonheur à notre table ! Nous vous attendons de pied ferme pour partager les doux breuvages qui couleront à flot de nos tonneaux ! Orgie de Bières garantie ! Nos nains, elfes et autres créatures mythiques vous recevront le mercredi 7 février à la MINP. Nous espérons que vos foies, estomacs et cordes vocales seront au rendez-vous ! On entend déjà au loin une voix rauque compter les secs de bière “... 18 ! 19 ! 20 ! …”`},
        {nom: 'Soirée vendredi 23h @LeDivino', photo: require('@/assets/img/events/soiree.jpg'), description: `PAF : 9€<br>Alors que la recherche pour l'anneau bat son plein, les membres de la Lord s'arrêtent en Tisengard, pour se reposer et découvrent un garde-manger empli de bières, eaux-de-vies  et de nourriture ! Ils se sentent alors OB-ligés de festoyer, et invitent ainsi tous les peuples de la Terre de l'Isère à venir se restaurer comme il se doit.`},
        {nom: 'Gondor\'s Games dimanche 14h30 @PPM', photo: require('@/assets/img/events/gondorsgames.jpg'), description: `Vous n’avez pas pu vous qualifier aux jeux olympiques de Tokyo ? Ne vous inquiétez pas, la Lord a organisé les Gondor’s Games rien que pour vous ! Le Rohan, le Mordor, l’Isengard et la Comté cherchent à former des équipes afin de conquérir la Terre du Milieu. Vous, champions, allez vous affronter dans diverses épreuves que la Lord vous a concocté. Alors, êtes-vous prêts à défendre vos couleurs ?`}
      ]
    }
  }
}
</script>

<style scoped>
.fade-enter-active {
  transition: all .3s ease;
}
.fade-leave-active {
  transition: all .3s ease;
}
.fade-enter{
  transform: translateX(+2000px);
  position: absolute;
}
.fade-leave-to {
  transform: translateX(-2000px);
  position: absolute;
}

img {
  max-width: 100%;
}

.chevron {
  text-align: center;
}
</style>



