<template>
  <div class="col s12">
    <div class="col s12">
      <div class="card-panel teal">
        <span class="white-text">Technophobe ? Le standard SOS de la Lord est dispo au 09.72.64.90.79 !</span>
      </div>
    </div>
    <TypeSos v-for="typeSos in typesSos" @select="$emit('selectionner', typeSos)"
             :key="typeSos.type"
             v-bind="typeSos"/>
  </div>
</template>

<script>
import TypeSos from './TypeSos'
const typesSos = require('@/assets/js/TypesSos')

export default {
  name: 'SelectionSos',
  components: {
    TypeSos
  },
  data () {
    return {
      typesSos
    }
  }
}
</script>



