<template>
  <div class="card">
    <div class="card-image waves-effect waves-block waves-light">
      <img class="activator" :src="photo">
    </div>
    <div class="card-content">
      <span class="card-title activator grey-text text-darken-4">{{ nom }}<i class="material-icons right">more_vert</i></span>
    </div>
    <div class="card-reveal">
      <span class="card-title grey-text text-darken-4">{{ nom }}<i class="material-icons right">close</i></span>
      <p v-html="description"></p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Event',
  props: ['description', 'nom', 'photo']
}
</script>

<style scoped>
img {
  max-width: 100%;
  object-fit: contain;
  bottom: 0;
  max-height: 50vh;
}

.card-image {
    max-height: 60% !important;
}

.card-image {
  background: black;
}

.card-reveal p {
  font-size: 1.25rem;
}
</style>



