<template>
  <div>
    <h3>Kfet trading - 0 fees !</h3>
    <div class="row">
      <h5>Recent trades</h5>
      <table class="striped highlight">
        <thead>
          <tr>
            <th>Time</th><th>Price</th><th>From</th><th>To</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="trade of trades.slice(0).slice(-10).sort((a,b) => a.time < b.time)">
            <td :key="trade.id">{{trade.time}}</td><td>{{trade.price}} € ({{ inBTC(trade.price) }} BTC) </td><td>{{trade.from}}</td><td>{{trade.to}}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="row">
      <h5>Order Book</h5>
      <div class="col l6 s12">
        <h6>Buying</h6>
        <table class="striped highlight">
          <thead>
            <tr>
              <th>Time</th><th>Who</th><th>Price</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="order of buying">
              <td :key="order.id">{{order.time}}</td><td>{{order.username}}</td><td>{{order.price}} € ({{ inBTC(order.price) }} BTC) </td><td>{{order.from}}</td><td>{{order.to}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="col l6 s12">
        <h6>Selling</h6>
        <table class="striped highlight">
          <thead>
            <tr>
              <th>Time</th><th>Who</th><th>Price</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="order of selling">
              <td :key="order.id">{{order.time}}</td><td>{{order.username}}</td><td>{{order.price}} € ({{ inBTC(order.price) }} BTC) </td><td>{{order.from}}</td><td>{{order.to}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="col l12 s12">
        <h5>New order</h5>
        <div class="row">
          <div class="input-field col s12">
            <select v-model="order">
              <option value="buy" selected>Buy</option>
              <option value="sell">Sell</option>
            </select>
            <label>Order</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s6">
            <input type="number" min="0.01" step="0.01" max="10000000" v-model="price" id="price"/>
            <label for="price">Price - in €</label>
          </div>
          <div class="input-field col s6">
            <input type="text" min="0.01" step="0.01" maxlength="15" v-model="username" id="price"/>
            <label for="price">Username</label>
          </div>
        </div>
        <div class="row withSend">
          <button class="btn waves-effect waves-light" @click="sendOrder">SEND ORDER
            <i class="material-icons right">send</i>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'Trading',
  sockets: {
    connect () {
      console.log('socket connected')
    },
    newOrder (order) {
      this.orders.push(order)
    },
    newTrade (trade) {
      this.trades.push(trade)
    },
    removeOrder (order) {
      this.orders = this.orders.filter(anOrder => anOrder.id !== order.id)
    }
  },
  computed: {
    selling () {
      return this.orders.filter(order => order.order === 'sell').sort((a, b) => a.price - b.price).splice(0, 10)
    },
    buying () {
      return this.orders.filter(order => order.order === 'buy').sort((a, b) => b.price - a.price).splice(0, 10)
    }
  },
  methods: {
    inBTC (euro) {
      return (euro / 6270).toFixed(5)
    },
    initValues () {
      this.$socket.emit('initValues', data => {
        this.orders = data.orders
        this.trades = data.trades
      })
    },
    sendOrder () {
      if (!this.username.length) return window.Materialize.toast('Username required', 1000)
      this.$socket.emit('newOrder', {order: this.order, price: this.price, username: this.username})
    }
  },
  data () {
    return {
      orders: [],
      trades: [],
      price: 7,
      username: '',
      order: 'buy'
    }
  },
  created () {
    this.initValues()
  },
  mounted () {
    $('select').material_select()
    $('select').on('change', _ => {
      this.order = $('select').val()
    })
  }
}
</script>

<style scoped>
.withSend {
  text-align: center;
}
</style>



