<template>
  <div id="team-vue">
      <transition name="bounce">
        <div v-if="selectedMember !== null" @click="unselectMember" id="materialbox-overlay" :style="'width:' + width + ';height:' + height">
          <img :src="'static/img/membres/' + selectedMember.photo" @click="unselectMember">
          <div class="description">
            {{ selectedMember.description }}
          </div>
        </div>
      </transition>
      <div class="row" id="selector-id">
        <Member v-for="(member, index) of members"
        @click.native="selectMember(index)"
        v-bind:index="index" v-bind="member" key="index"/>
      </div>
  </div>

</template>

<script>
import $ from 'jquery'
import Member from '@/components/client/Member'

export default {
  name: 'Team',
  components: {
    Member
  },
  data () {
    return {
      members: [
        {blaz: 'Gloria', role: 'Prez\'', photo: 'gloria.jpg', description: `Présidente la nuit, zombie le jour, vous la reconnaîtrez à son cri reconnaissable entre mille : “ALED”. Malgré le fait qu’elle se sente obligée de tout faire partout, c’est bien grâce à elle que la liste tient encore debout à ce jour. On dit que la puissance qu’elle retire du soutien infaillible de sa liste lui donne des pouvoirs mystiques, d’une puissance supérieure à 9000.`},
        {blaz: 'Antoine M.', role: 'Vice Prez\'', photo: 'menut.jpg', description: `VP de la liste, président des Scots. Son menu du jour est composé de whiskey, cul-sec, fortnite et sieste. Même s’il préfère enchaîner les tops 1 derrière sa console, son goût pour les bons alcools le pousse à sortir pour faire de nouvelles rencontres (en fait c’est un prétexte pour satisfaire sa curiosité oenologique).`},
        {blaz: 'Gaétan', role: 'Sécrétaire', photo: 'gaetan.jpg', description: `Ponceur de l’extrême et grimpeur émérite. On raconte qu’il n’a pas dormi depuis 10 ans. Convaincu qu’il ira en 1B alors qu’il va défoncer tous les partiels de l’année. Cette secrétaire ne s’arrête jamais. “Café ?”`},
        {blaz: 'Julien', role: 'Trez\'', photo: 'julien.jpg', description: `Membre à part entière du Gucci gang, cet elfe sous coke sait faire apparaître de l’argent de nulle part grâce à des procédés bien obscurs pour le reste des vivants. Heureusement pour nous, il distribue l’argent volontiers si vous avez la facture.
Au fait, t’as Circle ?`},
        {blaz: 'Guilhaume', role: 'Vice Trez\'', photo: 'guilhaume.jpg', description: `S’il brille chaque jour un peu plus dans l’art de troller, Guilhaume saura vous surprendre quand vous ne vous y attendiez plus. Après une bonne blague on s’y attache finalement, et on finit presque par oublier qu’il est tombé dans la soupe de la précampagne quand il était petit.`},
        {blaz: 'Thierry', role: 'Respo Financement', photo: 'thierry.jpg', description: `Tout droit venu de Suisse, notre Thierry manie l’argent comme personne, et sert la liste à bien des égards. On dit qu’il a tellement de contacts dans le monde du business qu’il doit blanchir son argent à travers la Lord of the Lists.`},
        {blaz: 'Aurélien', role: 'Respo SFX', photo: 'aurelien.jpg', description: `Homme pieux et spirituel, frère Aurélien a quitté le monastère pour évangéliser l’Imag. Il partage ses illuminations en réalisant des films bien stylés. Il a contaminé la Lord avec sa fameuse punchline : “ça part”.`},
        {blaz: 'Raoul', role: 'Respo Gondor\'s Games', photo: 'raoul.jpg', description: `Illuminant le soutien anglais par sa maîtrise parfaite de la langue de Shakespeare. Descendant tout droit de William Wallace, Raoul alterne avec brio les chansons paillardes et religieuses. Mac Houille Gauche, un des instigateurs de la révolution de la Scotlist, il a dû prêter allégeance aux Lords après sa défaite. Mais il attend patiemment son heure: one, two, twee, viva la Scotlist !`},
        {blaz: 'Andéol', role: 'Respo SOS', photo: 'andeol.jpg', description: `Ce pilier de bars sera toujours là pour t’accompagner lors de tes soirées (sauf si c’est une kfet ou qu’elle finit après 20h30). Son charisme est sublimé lorsqu’il a bu : humour fin, éloquence, agilité dans ses mouvements, délicatesse… il sera un respo SOS idéal (tu pourras même le raccompagner chez lui).`},
        {blaz: 'Romain', role: 'Prez\' JE', photo: 'romain.jpg', description: `Président ultra-charismatique de la J.E., il aime discuter avec tout ce qui bouge, y compris les listes adverses. Malgré ce défaut évident, son sourire et sa bonne humeur en font quelqu'un de très apprécié au sein de la liste.`},
        {blaz: 'Lucille', role: 'Graphiste', photo: 'lucille.jpg', description: `Graphiste hors pair, elfe émérite et cavalière confirmée, c’est elle qui donne une image à la liste. Toujours partante pour une soirée, son rire éclaire la Lord au quotidien.`},
        {blaz: 'Cyprien', role: 'Respo Partenariats', photo: 'cyprien.jpg', description: `Discret, fier responsable du trésor de la JE, Cyprien saura vous rendre service quand vous en avez besoin, et rire à vos blagues quand tous admettent qu’elles ne sont quand même pas terribles. Il saura vous faire remarquer si le moindre détail est de travers. Attachement garanti.`},
        {blaz: 'Mathieu', role: 'Respo Paris Sportifs', photo: 'mathieu.jpg', description: `Les bookmakers le détestent. Certains rapportent même qu’il serait responsable de quelques crises financières. Forcément, il boit pour célébrer ses exploits.`},
        {blaz: 'Antoine M.', role: 'Respo Goodies', photo: 'moreau.jpg', description: `Facilement reconnaissable grâce à sa pilosité imposante, ce cher Antoine vit de bière et de pâté. Oscar du meilleur acteur pour son rôle de nain dans le film et le teaser, on se demande encore de quelle liste il fait partie...`},
        {blaz: 'Antoine M.', role: 'Respo Technique', photo: 'murat.jpg', description: `Wesh elle est pourrie ma description ! De toute façon j'ai le dernier mot : je mets mon veto.`},
        {blaz: 'Adam', role: 'Respo SOS', photo: 'adam.jpg', description: `Lui qui ne lésine pas sur la vodka, saura tout de même sortir de son lit s’il le faut pour servir vos SOS. Seul nain ninja existant en ce monde, il peut se faire oublier plus vite qu'un CM de BPI.`},
        {blaz: 'Leïla', role: 'Respo P\'tit Dej\'', photo: 'leila.jpg', description: `Respo choré, respo p’tit dej, elle va probablement dormir pendant au moins 3 ans après les campagnes. N’hésitez donc pas à profiter de sa compagnie avant qu’il ne soit trop tard !`},
        {blaz: 'Baptiste', role: 'Respo Spam', photo: 'baptiste.jpg', description: `Aussi connu sous le nom de Spamtiste, son taux d'absence en CM (4 mois d’affilée !) n'a pour égal que son amour du pillage des snacks de la cafet. Ses SMS sont déjà blacklistés par 85% des membres de la liste. Il a également beaucoup trop de cheveux.`},
        {blaz: 'Nicolas', role: 'P\'tit Dej\'', photo: 'nicolas.jpg', description: `À mi chemin entre JCVD et l’Alsace, vous trouverez Nico. Ses grands écarts ou ses high-kicks vous laisseront bouche bée. Amateur de Kung-Fu, de mathématiques mais aussi de 8.6, cet alsacien de qualité n’aura de cesse de vous surprendre. NB : En jean, le grand écart bien sûr.`},
        {blaz: 'Yassine', role: 'Pôle Comm\'', photo: 'yassine.jpg', description: `Malgré son absentéisme chronique, ce fêtard invétéré connaît une bonne partie de l'Imag. Il est célèbre pour ses fins de soirées plutôt tardives. C’est également un excellent cuisinier.`},
        {blaz: 'Rémi', role: 'Respo Soirée', photo: 'remi.jpg', description: `Ce surhomme, capable de soulever la bastille entière, est avant tout un buveur hors-pair. En somme, c'est un nain (un peu grand, et sans la barbe). Il est aussi membre assidu du TPP où il s’y retrouve souvent en infériorité numérique, entouré de moult Atlas.`},
        {blaz: 'Matthias', role: 'Paris Sportifs', photo: 'matthias.jpg', description: `Vous avez besoin d’éplucher des légumes mais vous ne savez pas comment faire ? Matthias propose une alternative au Will Waller 2006. Vous cherchez une façon efficace de cuire votre plat ? Avec des années de scoutisme, Matthias a la solution. Il possède en effet une astuce de cuisine infaillible pour faciliter votre vie ; les cuisiniers le détestent !!`},
        {blaz: 'Dylan', role: 'Logistique', photo: 'dylan.jpg', description: `Un problème, des solutions. Dylan ne fait pas les choses à moitié, et le plus fou, c’est qu’il les fait toujours bien. Apportant son sourire et sa délicatesse bien à lui, il vous sera vite indispensable !`},
        {blaz: 'Théo', role: 'Respo Logistique', photo: 'theo.jpg', description: `Théo LaStreet ne passe pas par quatre chemin et ne fait pas dans le faux-semblant. Théal Larue le taciturne quant à lui sait se montrer enjoué après quelques bières. Son talent pour la logistique en fait un organisateur précieux approvisionnant la terre entière presque toujours à temps.`},
        {blaz: 'Théodore', role: 'Respo Soirée/Kfet/Dev', photo: 'theodore.jpg', description: `Si vous cherchez Théodore vers midi, il y a bien un endroit où vous le trouverez : La Cafet’. On ne compte plus les sandwichs préparés, ni les nettoyages ou encore les parties de tarot à son actif. Cet énergumène blond se trouve aussi être un staffeur de qualité, tant il est doux et attentionné ! Son humour, aussi subtil (certains diront merdique) soit-il, se manifeste à travers d’élégants jeux de mots en tout genre, qui ne manquent pas de provoquer des réactions allergiques ponctuées de “Ta gueule Théodore” des autres membres de la liste. Pour finir, Théodore est un grand amateur de Kfet. Il suffit de tendre l’oreille pour entendre ses cordes vocales résonner entre deux gorgées de bière.`},
        {blaz: 'Charlie', role: 'P\'tit Dej\'', photo: 'charlie.jpg', description: `Nous provenant tout droit de Condorcet, ce double listé Clint membre de la 4ème coloc saura vous surprendre ! Notre cher Charlie manie avec perfections les bases du Suédois, et balade ses doigts avec une folle aisance sur sa basse. En fait quand on y réfléchit, il aurait tout pour draguer.`},
        {blaz: 'Gautier', role: 'Respo Paris Sportifs', photo: 'gautier.jpg', description: `Sportif de haut niveau, Gautier a trouvé le bon filon pour réussir les paris. Quel que soit le sport ou la discipline, il est toujours là pour vous conseiller, grâce à sa connaissance des paris truqués (le foot et les arbitres…). A vos marques. Prêt. Pariez !`},
        {blaz: 'Loïc', role: 'Respo Film', photo: 'loic.jpg', description: `Doué de capacités inouïes en anglais, il exprime sa créativité à travers le (très gros) objectif de son super appareil photo. Toujours là pour goûter une dizaine de nouvelles bières, il peut parfois devenir une gentille fleur (ou un fucking FOUR) s’il se sent dans l’ambiance. Ne nettoyez pas le dance floor, lolo va s’en occuper.`},
        {blaz: 'Alexandre', role: 'Respo Communication', photo: 'alexandre.jpg', description: `Petit dernier des Lords, il s’est fait connaître par ses talents de ski, ou plutôt sa capacité innée à faire valser les gens. Possédant une verve peu commune, et un sens de la phrase, son rôle de respo comm’ était tout tracé. Ses concours mondialement connus à Grenoble sont des oeuvres d’art. Il publie plus vite que son ombre, donc soyez prêts à liker !`},
        {blaz: 'Thibault', role: 'Pôle Goodies', photo: 'thibault.jpg', description: `Sympathique AST, il est toujours accompagné de son vélo, qu’il ne s’est toujours pas fait voler ! De plus, ce magnifique petit barbu a réussi en moins d’une heure l’exploit d’apprendre à la prez à faire une roulade. Acteur de renom, cet homme est un poseur hors pair. Il aime tellement la cuisine et le sport que pour allier les deux, il n'hésite pas à laisser des ingredients au QG afin de faire un maximum d’aller-retour inutiles.`},
        {blaz: 'Matisse', role: 'Pôle Film', photo: 'matisse.jpg', description: `Compagnon floral de Loïc, cet alternant est un excellent assistant réalisateur. Doté d’un score Griffor impressionnant, il risque fortement de perdre à “Je n’ai jamais” (Ou de gagner, ça dépend du point de vue). Avec sa team, il n’hésitera pas une seconde à vous suivre sur un fort boyard (ou Mathieu Valbuena pour les intimes).`},
        {blaz: 'Anaël', role: 'Respo Kfet', photo: 'anael.jpg', description: `PapINP l’érudit.
Présent à l’INP depuis sa création, c’est l’homme que tout le monde écoute pour l’organisation des gros événements. On raconte qu’il possède un très grand secret pour garder sa jeunesse INPienne. King of Kfet, il est toujours là pour contrer les imprévus. Sa sagesse et son dévouement sont fortement appréciés et respectés. Ses Budgets Prévisionnels sont comparés au fameux tableau de Mona Lisa.`},
        {blaz: 'Denis', role: 'Pôle Gondor\'s Games', photo: 'denis.jpg', description: `Le Denis est un homme enjoué et sympathique. Grand amateur de l’humour noir, jaune, blanc, il n’hésitera pas cependant à vous faire remarquer que vos blagues ne sont pas marrantes, il n’apprécie que les plaisanteries de qualité. Malgré tout, ne vous lancez pas dans un débat avec lui, vous y laisserez vos plumes et votre chinois. Aussi surnommé Mac Houille Droite, il était l’autre membre fondateur de la Scotlist. En attendant de pouvoir putscher avec Raoul, il réside dans une des chambre dite du “pouilleux” à la Scotloc.`}
      ],
      selectedMember: null
    }
  },
  methods: {
    selectMember (index) {
      this.selectedMember = this.members[index]
    },
    unselectMember () {
      this.selectedMember = null
    },
    handleScroll () {
      $('div.card:visible:onScreen').removeClass('scale-out')
      this.unselectMember()
    }
  },
  computed: {
    width () {
      return window.innerWidth
    },
    height () {
      return window.innerHeight
    }
  },
  created () {
    $.expr[':'].onScreen = elem => {
      const $window = $(window)
      const viewportTop = $window.scrollTop()
      const viewportHeight = $window.height()
      const viewportBottom = viewportTop + viewportHeight
      const $elem = $(elem)
      const top = $elem.offset().top
      const height = $elem.height()
      const bottom = top + height
      return (top >= viewportTop && top < viewportBottom) ||
             (bottom > viewportTop && bottom <= viewportBottom) ||
             (height > viewportHeight && top <= viewportTop && bottom >= viewportBottom)
    }
    window.addEventListener('scroll', this.handleScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  },
  mounted () {
    setTimeout(_ => $('div.card:visible:onScreen').removeClass('scale-out'), 250)
  }
}
</script>

<style scoped>
#materialbox-overlay {
  opacity: 1;
  left: 0px;
  top: 0px;
  text-align: center;
}

#materialbox-overlay img {
  max-width: calc(100% - 50px);
  max-height: calc(95% - 100px - 50px);
  margin-top: 25px;
  margin-bottom: 25px;
}

.description {
  font-size: 1rem;
  color: white;
  max-width: 90%;
  text-align: center;
  margin: auto;
}

.bounce-enter-active {
  animation: bounce-in .5s;
}
.bounce-leave-active {
  animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}

</style>

<style>
.clickable {
  cursor: pointer;
}
</style>



